.item {
    display: inline-block; 
    margin: 10px;
}

.frame {
    display: flex;
    width: 80px;
    height: 80px;
    justify-content: center;
    align-items: center;
    background-color: #f7f7f7;
    border-radius: 20px;
    box-shadow: 3px 3px 5px 1px #58585885;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    transition: all .1s ease-in-out;
}

.frame:hover {
    background-color: #aecaff;
    transform: scale(1.1);
}

.icon {
    color: #242424;
    font-size: 60px;
}

.name {
    margin-top: 8px;
    font-size: 15px;
    color: #333333;
    font-weight: bold;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
}